	module.exports = {"data":{"markdownRemark":{"headings":[{"value":"Background","depth":2},{"value":"The Devil Game","depth":2},{"value":"7 Japanese Monsters","depth":2}],"html":"<h2>Background</h2>\n<p>Shiver Magazine is a magazine that focuses on all things horror related, be it real occurrences, fictitious stories, and anything in pop culture and media that fits in the category. Shiver has one rule, it won’t shy away from anything; the scarier the better. The content and visual style of Shiver is meant to embody that intense energy. </p>\n<p>![](../assets/shiver spreads 01.jpg)</p>\n<h2>The Devil Game</h2>\n<p>This is a fictional story that gives details on how to summon the Devil in order to play a game where you must outwit him in order to receive the answer to any question you desire. Due to the subject of the story being ritualistic in theme, the aesthetic for the article was inspired by old bibles and medieval lettering. The entire treatment is made up entirely of typography, using it as both text and imagery. </p>\n<h2>7 Japanese Monsters</h2>\n<p>This is a list of seven interesting urban legends and monsters unique to Japan. Taking inspiration from traditional Japanese art and calligraphy, the images in the article all match that illustrative style. The colour scheme uses the Japanese flag colours, white and red, in combination with black in order to draw out a darker mood to better suit the theme.</p>","frontmatter":{"title":"Shiver Magazine","graduate":"Martina Serafimovska","category":"Editorial Design","category2":"","category3":null,"image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/static/shiver cover-5755514b77edd27700efc0dae8e98e02-d03e3.jpg","srcSet":"/static/shiver cover-5755514b77edd27700efc0dae8e98e02-abc54.jpg 375w,\n/static/shiver cover-5755514b77edd27700efc0dae8e98e02-97c69.jpg 750w,\n/static/shiver cover-5755514b77edd27700efc0dae8e98e02-d03e3.jpg 1417w","sizes":"(max-width: 1417px) 100vw, 1417px"}}}}}},"pathContext":{"slug":"/work/shiver-magazine/","name":"/Shiver Magazine/"}}

